import { Component, OnInit, Input } from '@angular/core';
import { UnitsService } from '../../sites/services/units.service';
import { ActivatedRoute } from '@angular/router';
import { FormBuilder, FormControl, FormGroup, Validators, AbstractControl, ValidationErrors } from '@angular/forms';
import 'rxjs/Rx';
import { ValidatePassword, matchPassword, emailAvailability, phoneNumberAvailability } from './../../shared/common.validators';
import { ConfirmationService } from 'primeng/components/common/confirmationservice';
import { environment } from '../../../environments/environment';

@Component({
  selector: 'quadlogic-units',
  inputs: ['site'],
  templateUrl: './units.component.html',
  styleUrls: ['./units.component.css']
})

export class UnitsComponent implements OnInit {
  units: any[];
  display:boolean = false;
  site_id: any;
  meters = [];
  sections = [];
  unitForm: FormGroup;
  formType = "Create";
  editUnit:any;
  actions = [];
  msgs = [];
  upoladUrl;
  constructor(private unitService: UnitsService, private route: ActivatedRoute,
    private _fb:FormBuilder, private confirmationService:ConfirmationService) { 
    this.unitForm = this._fb.group({
      id: new FormControl(),
      meter: new FormControl(null,Validators.required),
      apartment_number: new FormControl(null,Validators.required),
      apartment_area: new FormControl(null,Validators.required),
      no_of_rooms:new FormControl(null,Validators.required),
      unit_sort_number:new FormControl(null,Validators.required),
      section:new FormControl(null,Validators.required)
    })
  }

  ngOnInit() {
    this.upoladUrl = environment.host;
    console.log(environment.host);
    this.route.params.subscribe(params => {
      this.site_id = +params['id'];
      
      this.unitService.getUnits(this.site_id)
      .subscribe(
        (data)=>{
          this.units = data.units;
        }
      )
      
      this.unitService.getSections(this.site_id).subscribe((res:any)=>{
        this.sections = res.sections;
      })

    })

    this.unitService.getMeters().subscribe((res:any)=>{
      this.meters = res.meters;
    })

  }
  
  createUnit(unitForm) {
    let units = [...this.units];
    if(unitForm.valid){
      this.unitService.createUnit(Object.assign(unitForm.value,{site_id: this.site_id})).subscribe((res:any)=>{
        if(unitForm.value.id){
          let index = units.indexOf(this.editUnit);
          units[index] = res.unit
        }else
        {
          units.unshift(res.unit);
        }
        this.units = units
        this.display = false;
      })
    }else{
      this.setFormTouched(unitForm);
    }
  }

  showDialog(){
    this.display = true;
    this.unitForm.reset();
    this.formType = "Create";
  }

  editDialog(unitId) {
    console.log(unitId);
    this.unitService.getUnitDetails(unitId).subscribe((res:any)=>{
      this.unitForm.setValue(res.unit);
      this.formType = "Edit"
      this.display = true;
    })
  }

  deleteDialog(unitId){

    // this.confirmationService.confirm({
    //   message: 'Are you sure that you want to proceed?',
    //   header: 'Delete this site',
    //   icon: 'fa fa-question-circle',
    //   accept: () => {
        this.unitService.deleteUnit(unitId).subscribe((res:any)=>{
          let units = [...this.units];
          let index = units.indexOf(this.editUnit)
          units.splice(index,1)
          this.units = units
        })
    //   },
    //   reject: () => {
    //       this.msgs = [{severity:'info', summary:'Rejected', detail:'You have rejected'}];
    //   }
    // });
  }

  // function for validate all form fields
  setFormTouched(form_obj: any) {
    // this._toastr.error("please fill all * marked fields", 'Oops!', { showCloseButton: true });
    Object.keys(form_obj.controls).forEach(field => {
      const control = form_obj.get(field);
      control.markAsTouched({ onlySelf: true });
    });
  }
  
  testAction(rowData: any){
    this.editUnit = rowData;
    this.actions = [
      {label: 'Edit', icon: 'far fa-pencil', command: () => {
          this.editDialog(rowData.id); 
      }},
      {label: 'Delete', icon: 'far fa-trash', command: () => {
        this.deleteDialog(rowData.id);
      }},
    ];
  }

  uploadHandler(event){
    console.log(event.files)
  }
}



