import { Component, OnInit, Input} from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators, AbstractControl, ValidationErrors } from '@angular/forms';
import 'rxjs/Rx';
import { ValidatePassword, matchPassword, emailAvailability, phoneNumberAvailability } from './../../shared/common.validators';
import { SectionsService } from '../../sites/services/sections.service';
import { ActivatedRoute } from '@angular/router';
import { ActivatedRouteSnapshot } from '@angular/router/src/router_state';
import { FormControlName } from '@angular/forms/src/directives/reactive_directives/form_control_name';
import {ConfirmationService} from 'primeng/api';
import { Message } from 'primeng/primeng';

@Component({
  selector: 'quadlogic-sections',
  templateUrl: './sections.component.html',
  styleUrls: ['./sections.component.css']
})
export class SectionsComponent implements OnInit {
  
  sections: any[];
  display:boolean = false;
  utilityTypes = [];
  formType:string;
  sectionForm:FormGroup;
  siteId:any;
  sectionType:string;
  actions: any[];
  beforeEditSection:any;
  msgs: any;
  site_id: any;

  constructor(private sectionService: SectionsService,private _fb: FormBuilder,
    private route:ActivatedRoute, private confirmationService: ConfirmationService) {
    
    this.sectionForm = this._fb.group({
      id: new FormControl(),
      section_name: new FormControl("",Validators.required),
      utility_type_id: new FormControl("",Validators.required),
      section_type: new FormControl("",Validators.required),
      utility_type: new FormControl()
    })

    this.route.params.subscribe(params=>{
      this.siteId = params['id'];
    })

  }

  ngOnInit() {
    this.route.params.subscribe(params => {
      this.site_id = +params['id'];
      console.log(this.site_id);
      this.sectionService.getSections(this.site_id)
        .subscribe(
          (data)=>{
            this.sections = data.sections;
          }
        )
        this.sectionService.getUtilityTypes()
        .subscribe(
          (data)=>{
            this.utilityTypes = data.utility_types;
          }
        )
    })  
  }

  showDialog(){
    this.display = true;
    this.sectionForm.reset();
    this.formType = "Create";
  }
  
  // create or update the section
  createSection(sectionForm) {
    this.msgs = [];
    let sections = [...this.sections];
    if(sectionForm.valid)
    {
      this.sectionService.createSection(Object.assign(sectionForm.value,{site_id:this.siteId})).subscribe((res:any)=>{
        
        if(res.success){
          this.msgs.push({severity:'success', summary:'Success', detail: res.message});
        }else{
          this.msgs.push({severity:'success', summary:'Error', detail: res.message});
        }

        if(sectionForm.value.id){
          let index = sections.indexOf(this.beforeEditSection);
          console.log(index);
          sections[index] = res.section
        }else
        {
          sections.unshift(res.section);
        }
        this.sections = sections;
        this.display = false;
      })
    }
    else {
      this.setFormTouched(sectionForm);
    }
  }


  editDialog(section){
    console.log(section);
    if(section.label){
     delete section.label
    }  
    this.beforeEditSection = section;      
    this.display = true;
    this.sectionForm.setValue(section);
    this.formType = "Edit";
  }

  // function for validate all form fields
  setFormTouched(form_obj: any) {
    // this._toastr.error("please fill all * marked fields", 'Oops!', { showCloseButton: true });
    Object.keys(form_obj.controls).forEach(field => {
      const control = form_obj.get(field);
      control.markAsTouched({ onlySelf: true });
    });
  }

  testAction(rowData: any){
    this.actions = [
      {label: 'Edit', icon: 'far fa-pencil', command: () => {
          this.editDialog(rowData); 
      }},
      {label: 'Delete', icon: 'far fa-trash', command: () => {
        this.deleteDialog(rowData);
      }},
    ];
  }

  deleteDialog(section){
    this.msgs = [];
    // this.confirmationService.confirm({
    //   message: 'Are you sure that you want to proceed?',
    //   header: 'Delete this section',
    //   icon: 'fa fa-question-circle',
    //   accept: () => {
        let sections = [...this.sections];
        this.beforeEditSection = section;
        this.sectionService.deleteSection(section.id).subscribe((res:any)=>{
          console.log(res);
          if(res.success){
            this.msgs.push({severity:'success', summary:'Success', detail: "Section deleted Successfully"});
          }else{
            this.msgs.push({severity:'success', summary:'Error', detail: "Something went wrong while deleting the section"});
          }
          let index = sections.indexOf(this.beforeEditSection);
          sections.splice(index,1);
          this.sections = sections;
    //     })
    //   },
    //   reject: () => {
    //       this.msgs = [{severity:'info', summary:'Rejected', detail:'You have rejected'}];
    //   }
    });
  }
}



