import { Component, OnInit, Input } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators, AbstractControl, ValidationErrors } from '@angular/forms';
import { ValidatePassword, matchPassword, emailAvailability, phoneNumberAvailability } from './../../shared/common.validators';
import { MeterPageService } from '../../sites/services/meter-page.service';
import { SectionsService } from 'app/sites/services/sections.service';
import 'rxjs/Rx';
import { ConfirmationService } from 'primeng/components/common/confirmationservice';
import { ActivatedRoute } from '@angular/router';
import { ActivatedRouteSnapshot } from '@angular/router/src/router_state';

@Component({
  selector: 'quadlogic-meter-page',
  templateUrl: './meter-page.component.html',
  styleUrls: ['./meter-page.component.css']
})

export class MeterPageComponent implements OnInit {
  
  meterForm: FormGroup;
  meters: any[];
  display:boolean = false;
  utilityTypes = [];
  actions = [];
  formType = 'Create;'
  editMeter:any; 
  msgs = []; 
  site_id: any;

  constructor(private meterPageService: MeterPageService,
    private _fb: FormBuilder,private sectionService:SectionsService,
    private confirmationService:ConfirmationService,
    private route:ActivatedRoute) {
      this.meterForm = this._fb.group({
        id: new FormControl(),
        meter_no: new FormControl(null,Validators.required),
        meter_type: new FormControl(null,Validators.required),
        meter_installation_date: new FormControl(null,Validators.required),
        meter_out_of_service_date: new FormControl(),
        multiplier: new FormControl(null,Validators.required),
        modifier: new FormControl(null,Validators.required),
        is_active: new FormControl(null,Validators.required)
      })
     }

  ngOnInit() {
    this.route.params.subscribe(params => {
      this.site_id = +params['id'];
      console.log(this.site_id);
        this.meterPageService.getMeters(this.site_id)
        .subscribe(
          (data)=>{
            this.meters = data.meters;
          }
        )

        this.sectionService.getUtilityTypes()
        .subscribe(
          (data)=>{
            this.utilityTypes = data.utility_types;
          }
        )
    })  
  }

  showDialog(){
    this.display = true;
    this.meterForm.reset();
    this.formType = "Create";
  }
  
  editDialog(meterId) {
    this.meterPageService.getMeterDetails(meterId).subscribe((res:any)=>{
      if(res.success)
      {
        res.meter.meter_installation_date = new Date(res.meter.meter_installation_date)
        res.meter.meter_out_of_service_date = new Date(res.meter.meter_out_of_service_date)
        this.meterForm.setValue(res.meter)
        this.display = true;
        this.formType = "Edit";
      }
      else{
        console.log(res);
      }
    })
  }

  deleteDialog(meterId){
    this.confirmationService.confirm({
      message: 'Are you sure that you want to proceed?',
      header: 'Delete this site',
      icon: 'fa fa-question-circle',
      accept: () => {
        this.meterPageService.deleteMeter(meterId).subscribe((res:any)=>{
          let meters = [...this.meters];
          let index = meters.indexOf(this.editMeter)
          meters.splice(index,1)
          this.meters = meters
        })
      },
      reject: () => {
          this.msgs = [{severity:'info', summary:'Rejected', detail:'You have rejected'}];
      }
    });
  }

  createMeter(meterForm) {
    let meters = [...this.meters];
    if(meterForm.valid){
      this.meterPageService.createMeter(meterForm.value, this.site_id).subscribe((res:any)=>{
        this.display = false;
        this.meterForm.reset();
        if(this.formType == "Edit")
        {
          let index = meters.indexOf(this.editMeter);
          meters[index] = res.meter;
        }
        else{
          meters.unshift(res.meter)
        }
        this.meters = meters;
      })
    }
    else{
      this.setFormTouched(meterForm);
    } 
  }

  // function for validate all form fields
  setFormTouched(form_obj: any) {
    // this._toastr.error("please fill all * marked fields", 'Oops!', { showCloseButton: true });
    Object.keys(form_obj.controls).forEach(field => {
      const control = form_obj.get(field);
      control.markAsTouched({ onlySelf: true });
    });
  }

  testAction(rowData: any){
    this.editMeter = rowData;
    this.actions = [
      {label: 'Edit', icon: 'far fa-pencil', command: () => {
          this.editDialog(rowData.id); 
      }},
      {label: 'Delete', icon: 'far fa-trash', command: () => {
        this.deleteDialog(rowData.id);
      }},
    ];
  }

}                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                                       


