import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs/Rx';
import { environment } from 'environments/environment';

@Injectable()
export class MeterPageService {

  constructor(private httpClient: HttpClient ) {}

  getMeters(site_id: string):Observable <any>{
    return this.httpClient.get(environment.host+"meters?site_id="+site_id);    
   }
  
  createMeter(reqBody:any, site_id: string):Observable <any> {
    return  this.httpClient.post(environment.host+"meters?site_id="+ site_id, reqBody);
  }

  getMeterDetails(reqBody:any):Observable <any> {
    return this.httpClient.get(environment.host+"meters/details/"+reqBody);
  }

  deleteMeter(reqBody:any):Observable <any> {
    return this.httpClient.delete(environment.host + "meters/"+reqBody)
  }
   
} 



