import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs/Rx';
import { environment } from 'environments/environment';

@Injectable()
export class UnitsService {
  constructor(private httpClient: HttpClient ) {}

  getUnits(site_id: string):Observable <any>{
    return this.httpClient.get(environment.host+"units", {params: {site_id: site_id }});    
   }
   
  getMeters():Observable <any>{
    return this.httpClient.get(environment.host+"unassigned_meters"); 
  }
  
  getSections(siteId):Observable <any>{
    return this.httpClient.get(environment.host + "assignable_sections?site_id="+siteId) 
  }

  createUnit(reqBody):Observable <any>{
    return this.httpClient.post(environment.host+"/units",reqBody);
  }

  getUnitDetails(reqBody):Observable <any> {
    return this.httpClient.get(environment.host+"units/details/"+reqBody)  
  }

  deleteUnit(reqBody):Observable <any> {
    return this.httpClient.delete(environment.host+"units/"+reqBody )
  }
  
}



