//Importing Internal modules
  import { CanActivate,CanActivateChild,ActivatedRouteSnapshot,RouterStateSnapshot,Router } from '@angular/router';
  import { Injectable } from '@angular/core';
  import { Observable } from 'rxjs/Observable';
//Importing Third-party modules

//Imporitng Application Modules

//Importing Application components

// Importing Application Services
  import { QuadlogicAuthService } from './auth.service';
import { RedirectionService } from './redirection.service';
import { UserService } from '../../shared/services/user.service';
import { TokenService } from './token.service';

//Any Other Please specify

@Injectable()
export class AuthGaurdService implements CanActivate ,CanActivateChild{

  constructor(private _auth:QuadlogicAuthService,private _router:Router,private _redirect:RedirectionService,private _user:UserService,private _tokenService:TokenService) { }

  //Checking if user is logged in or not
  canActivate(route:ActivatedRouteSnapshot,state:RouterStateSnapshot) : Observable<boolean>|Promise<boolean>|boolean{
    return this._auth.isAuthenticated().then(
     (authenticated:boolean)=>{
      console.log(authenticated);
        if(authenticated)
          return true;
        else{
          //  this._router.navigate(['/']);
          if(localStorage.getItem('refresh_token')!==null){
            this.updateRefreshToken(route);
          }else{
            this._router.navigate(['/']);
          }
         }
     }
   )
  }

  //Activating child routes
  canActivateChild(route: ActivatedRouteSnapshot,
    state: RouterStateSnapshot): Observable<boolean> | Promise<boolean> | boolean {
    return this._auth.isAuthenticated().then(
      (authenticated: boolean) => {        
        if (authenticated) {
          return true;
        } else {
          // this._router.navigate(['/']);
          // if user session expired on routing level. triggers re-login component for the user
        if(localStorage.getItem('refresh_token')!==null){
          this.updateRefreshToken(route);
        }else{
          this._router.navigate(['/']);
        }


        }
      }
    )
  }

  updateRefreshToken(route){
    this._auth.updateRefreshToken()
    .subscribe((res)=>{
      console.log(res);
      
      if(res['success']){

        // this._tokenService.storeTokens(
        //   res["authentication_token"],
        //   res["refresh_token"]
        // );
      }
      this._router.navigate([route['_routerState']['url']])
    })
  }
}



