import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs/Rx';
import { environment } from 'environments/environment';


@Injectable()
export class SitesServiceService {

  constructor(private httpClient: HttpClient) {

  }

  getSites():Observable <any>{
   return this.httpClient.get(environment.host+"sites");     
  }

  // sekhar
  // To get the site details

  getSiteDetails(id: any):Observable <any>{
    return this.httpClient.get(environment.host+"get_stie_details",{params:{id: id}});
  }

  createSite(reqBody:any):Observable <any>{
    return this.httpClient.post(environment.host+"/sites",reqBody)
  }

  updateSite(reqBody:any):Observable <any> {
    return this.httpClient.put(environment.host+"sites/"+reqBody.id,reqBody);
  }

  deleteSite(siteId:any):Observable <any> {
    return this.httpClient.delete(environment.host+"sites/"+siteId);
  }
}



