//Importing Internal modules
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { RouterModule } from '@angular/router';

//Importing Third-party modules
// import { FullCalendarModule } from 'ng-fullcalendar';
import { ModalModule } from 'ngx-bootstrap';
import { NgSelectModule } from '@ng-select/ng-select';

//Imporitng Application Modules

//Importing Application components
import { ReLoginComponent } from './re-login/re-login.component';
import { DropdownComponent } from './dropdown/dropdown.component';
//Importing Application Service
import { SweetAlertService } from './services/sweet-alert.service';
//Anyother
import { FileSelectDirective, FileDropDirective } from 'ng2-file-upload';
import { DateFormatterPipe } from "./filters/date-formatter.pipe";
import { LoadingImgDirective } from "./directives/loading-image.directive";
import { FileUploadModule } from 'ng2-file-upload';
import { RoleSelectComponent } from './role-select/role-select.component';

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    ModalModule,
    ReactiveFormsModule,
    // InfiniteScrollModule,
    // FullCalendarModule,
    RouterModule,
    FileUploadModule,
    NgSelectModule
  ],
  declarations: [
    ReLoginComponent,
    DropdownComponent,
    LoadingImgDirective,
    DateFormatterPipe,
     RoleSelectComponent,
  ],
  entryComponents: [
    ReLoginComponent
  ],
  exports:[
    DropdownComponent,
    LoadingImgDirective,
    // FileDropDirective,
    // FileSelectDirective,
    DateFormatterPipe,
  ],
  providers: [SweetAlertService]
})
export class SharedModule { }



