import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { SitesRoutingModule } from './sites-routing.module';
import { DataTableModule} from 'primeng/datatable';
import { TabViewModule} from 'primeng/tabview';
import { DialogModule} from 'primeng/dialog';
import { FileUploadModule} from 'primeng/fileupload';
import { DropdownModule} from 'primeng/dropdown';
import { BreadcrumbModule} from 'primeng/breadcrumb';
import { TableModule} from 'primeng/table';
import { SplitButtonModule} from 'primeng/splitbutton';
import { CalendarModule} from 'primeng/calendar';
import { InputSwitchModule} from 'primeng/inputswitch';

import { SitesListComponent } from './sites-list/sites-list.component';
import { SitesServiceService } from './services/sites-service.service';
import { UnitsService } from './services/units.service';
import { SectionsService } from './services/sections.service';
import { TenantsService } from './services/tenants.service';
import { MeterPageService } from './services/meter-page.service';
import { SiteComponent } from './site/site.component';
import { SectionsComponent } from './sections/sections.component';
import { OverviewComponent } from './overview/overview.component';
import { MeterPageComponent } from './meter-page/meter-page.component';
import { UnitsComponent } from './units/units.component';
import { TenantsComponent } from './tenants/tenants.component';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { UserService } from './services/user.service';
import { ButtonModule } from 'primeng/button';
import {GrowlModule} from 'primeng/growl';

import {ConfirmDialogModule} from 'primeng/confirmdialog';
import {ConfirmationService} from 'primeng/api';


@NgModule({
  imports: [
    CommonModule,
    SitesRoutingModule,
    DataTableModule,
    DataTableModule,
    TabViewModule,
    DialogModule,
    FileUploadModule,
    DropdownModule,
    BreadcrumbModule,
    TableModule,
    SplitButtonModule,
    FormsModule,
    ReactiveFormsModule,
    CalendarModule,
    ButtonModule,
    ConfirmDialogModule,
    GrowlModule,
    InputSwitchModule
  ],
  declarations: [SitesListComponent, SiteComponent, SectionsComponent, OverviewComponent, MeterPageComponent, UnitsComponent, TenantsComponent],
  providers: [SitesServiceService, UnitsService, MeterPageService, SectionsService, TenantsService, ConfirmationService, UserService]
})

export class SitesModule { }



