//Importing Internal modules
  import { Component, OnInit } from '@angular/core';
  import { ActivatedRoute, Router} from '@angular/router';

//Importing Third-party modules
  import { ToastsManager } from 'ng2-toastr/ng2-toastr';

//Imporitng Application Modules

//Importing Application components

// Importing Application Services
  import { QuadlogicAuthService } from './../services/auth.service';
  import { UserService } from './../../shared/services/user.service';
  import { TokenService } from './../services/token.service';
import { PreloaderService } from '../../shared/services/preloader.service';

//Any Other Please specify

@Component({
  selector: 'eteki-confirm-email',
  templateUrl: './confirm-email.component.html',
  styleUrls: ['./confirm-email.component.scss']
})
export class ConfirmEmailComponent implements OnInit {

  confirmKey:any;

  constructor(
    private _toastr:ToastsManager,
    private _auth:QuadlogicAuthService,
    private _activatedRoute:ActivatedRoute,
    private _router:Router,
    private _tokenService:TokenService,
    private _userService:UserService,
  private _preLoader: PreloaderService) { }

  //Checking if any logged in user if not we will validate token and redirect to login page
  ngOnInit() {
    if(this._tokenService.getAuthenticationToken() === null && this._tokenService.getRefreshToken() === null){
      this._preLoader.open();
      this._activatedRoute.queryParams
      .subscribe(params => {
        this.confirmKey = params['key'];
        if(this.confirmKey !== undefined){
          var confirmToken = {confirmation_token:this.confirmKey};
          this._auth.confirmEmail(confirmToken).subscribe(
            (res) =>{
              this._preLoader.close();
              if(res['success']){
                this._toastr.success(res['message'][0], 'Success', { 'showCloseButton': true });
                this._router.navigate(['/auth/login']);
              }else{
                this._toastr.error(res['message'][0], 'Oops!', { 'showCloseButton': true });
                this._router.navigate(['/auth/login']);
              }
            } ,
            (err) => {
              this._preLoader.close();
              this._toastr.error(err, 'Oops!', { 'showCloseButton': true });
            }
          )
        }
        else{
          this._preLoader.close();
          this._toastr.error('Confirm key not provided', 'Oops!', { 'showCloseButton': true });
          this._router.navigate(['/auth/login']);
        }
      })
    }
    else{
      this._toastr.error('Cannot confirm email while logged in', 'Oops!', { 'showCloseButton': true });
      this._router.navigate(['/']);
    }
  }

}



