import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs/Rx';
import { environment } from 'environments/environment';

@Injectable()
export class SectionsService {
  constructor(private httpClient: HttpClient ) {}

  getSections(site_id: string):Observable <any>{
    return this.httpClient.get(environment.host+"sections?site_id="+site_id);    
  }

  getUtilityTypes():Observable <any> {
    return this.httpClient.get(environment.host + "utility_types");
  }

  createSection(reqBody:any):Observable <any> {
    return this.httpClient.post(environment.host+ "sections",reqBody);
  }

  deleteSection(reqBody:any):Observable <any> {
    return this.httpClient.delete(environment.host+"sections/"+reqBody)
  }
  
}



