import { Component, OnInit, Input } from '@angular/core';
import { TenantsService } from '../../sites/services/tenants.service';
import { FormBuilder, FormControl, FormGroup, Validators, AbstractControl, ValidationErrors } from '@angular/forms';
import 'rxjs/Rx';
import { ValidatePassword, matchPassword, emailAvailability, phoneNumberAvailability } from './../../shared/common.validators';
import { Console } from '@angular/core/src/console';

import {ConfirmationService} from 'primeng/api';
import {ButtonModule} from 'primeng/button';
import {ConfirmDialogModule} from 'primeng/confirmdialog';
import {DropdownModule} from 'primeng/dropdown';
import {CalendarModule} from 'primeng/calendar';
import { ActivatedRoute } from '@angular/router';
import { log } from 'util';

@Component({
  selector: 'quadlogic-tenants',
  inputs: ['site'],
  templateUrl: './tenants.component.html',
  styleUrls: ['./tenants.component.css']
})
export class TenantsComponent implements OnInit {
  tenantCreateForm: FormGroup;
  actions: any[];
  tenantId:any = 0;
  msgs:any;

  @Input() site: any;

  tenants: any[];
  display:boolean = false;
  units: any[];
  truthy: any[];
  tenant_types: any[];
  selectedUnit: any;
  site_id: any;
  tenant_copy: any = {};
  keys: any;
  status_truthy = []
  constructor(private tenantService: TenantsService,
    private _fb: FormBuilder, private confirmationService: ConfirmationService, private route: ActivatedRoute) { 
      this.tenantCreateForm = this._fb.group({
        // id: new FormControl(),
        first_name: new FormControl(null,Validators.required),
        last_name: new FormControl(null, Validators.required),
        email: new FormControl(null, Validators.required),
        status: new FormControl(null, Validators.required),
        unit_id: new FormControl(null, Validators.required),
        tenant_type: new FormControl(null, Validators.required),
        move_in_date: new FormControl(null, Validators.required),
        move_out_date: new FormControl(),
        adult_occupants_count: [0,Validators.required],
        minor_occupants_count: [0, Validators.required],
        fix_occupants_count: new FormControl(null, Validators.required),
        is_late_fee_applicable: new FormControl(null, Validators.required),
        floor_plan_id: new FormControl(null, Validators.required),
        property_management_floor_plan_id: new FormControl(),
        property_management_property_id: new FormControl(),
        property_management_unit_id: new FormControl(),
        property_management_resident_id: new FormControl(),
        property_management_lease_id: new FormControl(),
        starting_balance: new FormControl(null, Validators.required),
        starting_balance_date: new FormControl(null, Validators.required),
        billing_date: new FormControl(null,Validators.required),
        is_active: new FormControl(null, Validators.required)
      });
     }

  ngOnInit() {
    this.route.params.subscribe(params => {
      this.site_id = +params['id'];
      this.tenantService.getTenants(this.site_id)
      .subscribe(
        (data)=>{
          this.tenants = data.tenants;
          this.units = data.units;
        })
      })
      this.truthy = [{ label: "Y", value: true}, { label: "N", value: false}]
      this.status_truthy = [{ label: "Active", value: true}, { label: "Inactive", value: false}]                    
    }

  createTenant(tenantCreateForm){
      if(tenantCreateForm.valid){ 
        if(this.tenantId  == 0)
        {
          this.tenantService.createTenant(tenantCreateForm.value, this.site_id).subscribe((res:any)=>{
            console.log(res);
            this.tenant_response(res);
          })
        }
        else {
          this.tenantService.updateTenant(tenantCreateForm.value, this.site_id, this.tenantId).subscribe((res:any)=>{
            console.log(res);
            this.tenant_response(res);
          })
        }
      }    
    else{
      this.setFormTouched(this.tenantCreateForm);  
    }
  }

  // Response for create and update
  tenant_response(res){
    this.tenants = res.tenants;
    this.units = res.units;
    this.msgs = [];
    
    if(res.tenant != undefined){  
      this.tenants.unshift(res.tenant);
    }

    if(res.success){
      this.msgs.push({severity:'success', summary:'Success', detail: res.message});
    }else{
      this.msgs.push({severity:'success', summary:'Error', detail: res.message});
    }

    this.display = !res.success;
    this.tenantCreateForm.reset();
  }
  
  // function for validate all form fields
  setFormTouched(form_obj: any) {
    // this._toastr.error("please fill all * marked fields", 'Oops!', { showCloseButton: true });
    Object.keys(form_obj.controls).forEach(field => {
      const control = form_obj.get(field);
      control.markAsTouched({ onlySelf: true });
    });
  }

  editTenant(tenant: any){
    this.actions = [
      {label: 'Edit', icon: 'far fa-pencil', command: () => {
              this.editTenantDialog(tenant)
      }},
      {label: 'Delete', icon: 'far fa-trash', command: () => {
          this.deleteDialog(tenant)
      }},
    ];
  }

  showDialog(){
    this.tenantCreateForm.reset();
    this.tenantCreateForm.controls["adult_occupants_count"].setValue(0);
    this.tenantCreateForm.controls["minor_occupants_count"].setValue(0);
    this.display = true;
    this.tenantId = 0;
  }

  editTenantDialog(tenant) {
    this.tenantId = tenant.id 
    this.keys = Object.keys(tenant);
    let key = "";
    for(let i=0; i < this.keys.length; i++){
      key = this.keys[i] 
      let date_keys = ["move_in_date", "move_out_date", "starting_balance_date", "billing_date"]
      
      if(date_keys.indexOf(key) != -1){
        tenant[key] = new Date(tenant[key]) 
      }

      let truthy_keys = ["is_late_fee_applicable", "status", "is_active"]
      if(truthy_keys.indexOf(key) != -1)
      {
        // To make the truty values same as truthy object
        if(tenant[key] != null){
          let tempKey = tenant[key].toString() 
          let lableTempKey = tempKey.charAt(0).toUpperCase()+ tempKey.slice(1);
          let tempValue = tenant[key];
          tenant[key] = { label: lableTempKey, value: tempValue}
        }  
      }
          
      this.tenant_copy[key] = tenant[key]
    }
    
    

    // two fields not required for tenants edit
    delete this.tenant_copy.id
    delete this.tenant_copy.name
    delete this.tenant_copy.unit
    delete this.tenant_copy.moveindate
    delete this.tenant_copy.billingdate
    
    this.tenantCreateForm.setValue(this.tenant_copy);
    this.display = true;
  }

  deleteDialog(tenant) {
    console.log(tenant);
    this.tenantId = tenant.id 
    this.confirmationService.confirm({
      message: 'Are you sure that you want to proceed?',
      header: 'Delete this tenant',
      icon: 'fa fa-question-circle',
      accept: () => {
        this.tenantService.deleteTenant(this.tenantId, this.site_id).subscribe((res:any)=>{
          this.tenant_response(res)
          // for(let i=0;i<this.tenants.length;i++)
          // {
          //   if(this.tenants[i].id == this.tenantId)
          //   {
          //     this.tenants.splice(i,1);
          //     break;
          //   }
          // }
        })
      },
      reject: () => {
          this.msgs = [{severity:'info', summary:'Rejected', detail:'You have rejected'}];
      }
    });
  }
}



