// Importing Internal modules
  import { NgModule } from '@angular/core';
  import { RouterModule, Routes } from '@angular/router';

// Importing Third-party modules

// Imporitng  Application Modules

//Importing Application components
  import { LoginComponent } from './login/login.component';
  import { ConfirmEmailComponent } from './confirm-email/confirm-email.component';
  import { DashboardComponent } from './dashboard/dashboard.component'
  import { ForgotPasswordComponent } from './forgot-password/forgot-password.component';
  import { ResendConfirmEmailComponent } from './resend-confirm-email/resend-confirm-email.component';

// Importing Application Services
  import { AuthLoginGaurdService } from '../auth/services/auth-login-gaurd.service';

const authRoutes: Routes = [
    { path: 'auth',component: DashboardComponent, canActivateChild : [AuthLoginGaurdService],children:[
        { path: '', redirectTo:'login',pathMatch:'full'},
        { path: 'login', component: LoginComponent},
        { path: 'confirm_email', component: ConfirmEmailComponent},
        { path: 'forgot_password', component: ForgotPasswordComponent},
        { path :'resend_confirm_email',component:ResendConfirmEmailComponent},
        { path :'reset_password',component:ForgotPasswordComponent},
        { path: '**', redirectTo:'login',pathMatch:'full'}
    ]}
];

@NgModule({
    imports: [
        RouterModule.forChild(authRoutes)
    ],
    exports: [RouterModule]
})
export class AuthRoutingModule {}



