//Importing Internal modules
  import { Router } from '@angular/router';
  import { Component, OnInit } from '@angular/core';
  import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';


//Importing Third-party modules
  import { ToastsManager } from 'ng2-toastr/ng2-toastr';

//Imporitng Application Modules

//Importing Application components

// Importing Application Services
  import { QuadlogicAuthService } from './../services/auth.service';
import { PreloaderService } from '../../shared/services/preloader.service';

//Any Other Please specify


@Component({
  selector: 'eteki-resend-confirm-email',
  templateUrl: './resend-confirm-email.component.html',
  styleUrls: ['./resend-confirm-email.component.scss']
})

export class ResendConfirmEmailComponent implements OnInit {
  resendConfirmEmailForm:FormGroup;
  constructor(
    public _toastr: ToastsManager,
    private fb: FormBuilder,
    private _auth: QuadlogicAuthService,
    private _router:Router ,
    private _preLoader: PreloaderService
  ) {
    this.resendConfirmEmailForm =this.fb.group({
      'email': new FormControl(null, [Validators.required, Validators.pattern("^[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,4}$")]),
    });
  }

  ngOnInit() {
  }

  resendConfirmEmail(resendEmailForm){
    if(resendEmailForm.valid){
      this._preLoader.open();
      this._auth.resendConfirmEmail(resendEmailForm.value).subscribe(
        (res:Response) => {
          if(res['success']){
            this._preLoader.close();
            this._toastr.success(res['message'][0], 'Success', { 'showCloseButton': true });
            this._router.navigate(['/auth/login']);
          }else{
            this._preLoader.close();
            this._toastr.error(res['message'][0], 'Oops!', { 'showCloseButton': true });
          }
        }
        , (err) => {
          this._preLoader.close();
          this._toastr.error(err, 'Oops!', { 'showCloseButton': true });
        }
      )
    }else{
      this.setFormTouched(resendEmailForm)
    }

  }

  // function for validate all form fields
  setFormTouched(form_obj: any) {
    this._toastr.error("please fill all * marked fields", 'Oops!', { showCloseButton: true });
    Object.keys(form_obj.controls).forEach(field => {
      const control = form_obj.get(field);
      control.markAsTouched({ onlySelf: true });
    });
  }
}



