import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Observable } from 'rxjs/Rx';
import { environment } from 'environments/environment';

@Injectable()
export class TenantsService {
  constructor(private httpClient: HttpClient ) {}
  
  // sahu
  getTenants(site_id: string):Observable <any>{
    return this.httpClient.get(environment.host+"tenants/"+site_id);    
   }

  // To get the tenant details
  getTenantDetails(id: any):Observable <any>{
    return this.httpClient.get(environment.host+"get_site_details",{params:{id: id}});
  }

  createTenant(reqBody:any, site_id: string):Observable <any>{
    return this.httpClient.post(environment.host+"create_tenant",{params: reqBody, site_id: site_id})
  }

  updateTenant(reqBody:any, site_id: string, id: string):Observable <any>{
    return this.httpClient.post(environment.host+"create_tenant",{params: reqBody, site_id: site_id, id: id})
  }

  deleteTenant(tenantId:any, site_id: string):Observable <any> {
    return this.httpClient.delete(environment.host+"tenants/"+tenantId, {params: { site_id: site_id }});
  }

}



