// Importing Internal modules
import { AbstractControl, FormControl, ValidationErrors } from '@angular/forms';
import { Observable } from 'rxjs/Observable';
// Importing Third-party modules

// Imporitng Application Modules

// Importing Application components

// Importing Application Services
import { QuadlogicAuthService } from './../auth/services/auth.service';

export function ValidatePassword(control: AbstractControl) {
  if (!control.value.includes('@') && control.value !== "") {
    return { validPassword: true };
  }
  return null;
}

// function for to check email availbility

export function emailAvailability(control: AbstractControl) {
  if (!control.valueChanges) {
    return Observable.of(null);
  } else {
    return control.valueChanges
      .debounceTime(100)
      .distinctUntilChanged()
      .switchMap(value => this._auth.emailAvailability({ email: control.value }))
      .map(res => {
        return res['success'] == false ? { invalidEmail: true } : null;
      }).first()
  }
}

// function for to check phone number availbility

export function phoneNumberAvailability(control: AbstractControl) {
  if (!control.valueChanges) {
    return Observable.of(null);
  } else {
    return control.valueChanges
      .debounceTime(500)
      .distinctUntilChanged()
      .switchMap(value => this._auth.phoneNumberAvailability({ phone: control.value }))
      .map(res => {
        return res['success'] == false ? { invalidNumber: true } : null;
      }).first()
  }
}

  // function for to compare passwords

export function matchPassword(AC: AbstractControl) {
  let password = AC.get('password').value; // to get value in input tag
  let confirmPassword = AC.get('confirm_password').value; // to get value in input tag
  if (password != confirmPassword) {
    AC.get('confirm_password').setErrors({ MatchPassword: true })
  } else {
    return null
  }
}


export const duplicateEmail = (control: AbstractControl): { [key: string]: boolean } => {
  const email = control.get('email').value;
  const alternate_email = control.get('alternate_email').value

  if (email === alternate_email) {
    return { 'emailMatched': true }
  } else {
    return null
  }
};


export const duplicateContact = (control: AbstractControl): { [key: string]: boolean } => {
  const contact1 = control.get('contact1').value;
  const contact2 = control.get('contact2').value
  if (contact1 === contact2) {
    return { 'contactMatched': true }
  } else {
    return null
  }

}



