// Importing Internal modules
import { Observable } from 'rxjs/Observable';
import { Response } from '@angular/http';
import { Component, OnInit, TemplateRef, ViewChild } from '@angular/core';
import { NgForm } from '@angular/forms/src/directives';
import { FormBuilder, FormControl, FormGroup, Validators, AbstractControl } from '@angular/forms';
import { Router } from '@angular/router';
// Importing Application Services
import {
  ValidatePassword,
  emailAvailability,
  matchPassword
} from "./../../shared/common.validators";

import { QuadlogicAuthService } from "../../auth/services/auth.service";
import { TokenService } from "../../auth/services/token.service";

@Component({
  selector: "eteki-role-select",
  templateUrl: "./role-select.component.html",
  styleUrls: ["./role-select.component.scss"]
})
export class RoleSelectComponent implements OnInit {
  roleForm: FormGroup;
  userObj: {};

  constructor(
    private fb: FormBuilder,
    private login: QuadlogicAuthService,
    private router: Router,
    private _tokenService: TokenService
  ) {
    this.roleForm = this.fb.group({
      default_role: new FormControl(null, Validators.required)
    });
  }
  ngOnInit() {

  }

}



