import { Ng4LoadingSpinnerModule } from 'ng4-loading-spinner';
//Importing Internal modules
  // import { BrowserModule } from '@angular/platform-browser';
  import { NgModule } from '@angular/core';
  import { CommonModule } from '@angular/common';
  import { FormsModule, ReactiveFormsModule } from '@angular/forms';
  import { HttpModule } from '@angular/http';

//Importing Third-party modules
  import { ToastModule } from 'ng2-toastr/ng2-toastr';

//Imporitng Application Modules
  import { AuthRoutingModule } from './auth-routing.module';
  import { SharedModule } from './../shared/shared.module';
  import { ToastsManager } from 'ng2-toastr/ng2-toastr';
//Importing Application components
  import { LoginComponent } from './login/login.component'
  import { ConfirmEmailComponent } from './confirm-email/confirm-email.component';
  import { ResendConfirmEmailComponent } from './resend-confirm-email/resend-confirm-email.component';
  import { ForgotPasswordComponent } from './forgot-password/forgot-password.component';
  import { BootstrapModalModule } from 'ng2-bootstrap-modal';
  import { BsDatepickerModule, TimepickerModule, ModalModule, BsDropdownModule } from 'ngx-bootstrap';
  import { DashboardComponent } from 'app/auth/dashboard/dashboard.component';
  
// prime ng components
import {DialogModule} from 'primeng/dialog';
import {FileUploadModule} from 'primeng/fileupload';
import {DropdownModule} from 'primeng/dropdown';
import {MenubarModule} from 'primeng/menubar';
import {TableModule} from 'primeng/table';
import {ButtonModule} from 'primeng/button';
import {AccordionModule} from 'primeng/accordion';
import {InputTextModule} from 'primeng/inputtext';
import {PasswordModule} from 'primeng/password';
import {TabViewModule} from 'primeng/tabview';
import {DataTableModule} from 'primeng/datatable';
import {GrowlModule} from 'primeng/growl';

// Importing Application Services
@NgModule({
  imports: [
    CommonModule,
    AuthRoutingModule,
    FormsModule,
    ReactiveFormsModule,
    HttpModule,
    ToastModule.forRoot(),
    SharedModule,
    BsDatepickerModule.forRoot(),
    TimepickerModule.forRoot(),
    BootstrapModalModule.forRoot({ container: document.body }),
    Ng4LoadingSpinnerModule.forRoot(),
    ModalModule.forRoot(),
    BsDropdownModule.forRoot(),
    DialogModule,
    FileUploadModule,
    DropdownModule,
    MenubarModule,
    TableModule,
    ButtonModule,
    AccordionModule,
    InputTextModule,
    PasswordModule,
    TabViewModule,
    DataTableModule,
    GrowlModule
  ],
  declarations: [LoginComponent, ConfirmEmailComponent, ForgotPasswordComponent, ResendConfirmEmailComponent,DashboardComponent],
  providers: []
})
export class AuthModule { }



