import { Directive, ElementRef, AfterContentInit, HostBinding, ViewChild, HostListener, Input, Output, EventEmitter, OnInit, OnChanges, QueryList } from '@angular/core';

@Directive({
    selector: '[tohLoadingImg]'
})
export class LoadingImgDirective implements OnChanges {
    @Output() userUpdated = new EventEmitter();
    @Input('src') @HostBinding() src: string;
    @Input('tempVariable') tempVariable: any;
    constructor(private el: ElementRef) { }
   @HostListener('document:click', ['$event.target'])
    public onClick(href) {
        console.log(this.tempVariable);

        let extension= this.tempVariable.split('.').pop()
        switch(extension){
            case 'doc':
              this.src =  'assets/images/word.png'
            break;
            case 'pdf':
              this.src= 'assets/images/android.png'
            break;
            default:
            //this.src='assets/images/word.png'
            break;
        }
        //this.src  = 'assets/images/word.png';
        this.userUpdated.emit(this.src)
    }
    ngOnChanges() {
      console.log(this.tempVariable);

       let extension= this.tempVariable.split('.').pop()
        switch(extension){
            case 'doc':
              this.src =  'assets/images/word.png'
            break;
            case 'pdf':
              this.src= 'assets/images/android.png'
            break;
            default:
            //this.src='assets/images/word.png'
            break;
        }
          this.userUpdated.emit(this.src)
    }
}



