import { BrowserModule } from '@angular/platform-browser';
import { BrowserAnimationsModule} from '@angular/platform-browser/animations';
import { NgModule } from '@angular/core';
import { FormsModule , ReactiveFormsModule} from '@angular/forms';
import { HttpModule } from '@angular/http';
import { SitesModule } from '../app/sites/sites.module';
import { AppComponent } from './app.component';
import { AppRoutingModule} from './app-routing.module';
import { HeaderComponent } from './header/header.component';
import { HttpClientModule } from '@angular/common/http';
import { AppConstant } from 'app/app.constant';
import { AuthModule } from 'app/auth/auth.module';

// services used in quadlogic for all models
import { AppInitService } from 'app/shared/services/app-init.service';
import { TokenInterceptor } from 'app/auth/services/token.interceptor';
import { TokenService } from 'app/auth/services/token.service';
import { AuthLoginGaurdService } from 'app/auth/services/auth-login-gaurd.service';
import { AuthGaurdService } from 'app/auth/services/auth-gaurd.service';
import { RedirectionService } from 'app/auth/services/redirection.service';
import { QuadlogicAuthService } from 'app/auth/services/auth.service';
import { HTTP_INTERCEPTORS } from '@angular/common/http';
import { APP_INITIALIZER } from '@angular/core';
import { UserService } from 'app/shared/services/user.service';
import { ToastModule } from 'ng2-toastr';
import { PreloaderService } from 'app/shared/services/preloader.service';

  // prime ng components
import {DialogModule} from 'primeng/dialog';
import {FileUploadModule} from 'primeng/fileupload';
import {DropdownModule} from 'primeng/dropdown';
import {MenubarModule} from 'primeng/menubar';
import {TableModule} from 'primeng/table';
import {ButtonModule} from 'primeng/button';
import {AccordionModule} from 'primeng/accordion';
import {InputTextModule} from 'primeng/inputtext';
import {PasswordModule} from 'primeng/password';
import {TabViewModule} from 'primeng/tabview';
import {DataTableModule} from 'primeng/datatable';
import { Ng4LoadingSpinnerModule } from 'ng4-loading-spinner';

export function init_app(appLoadService: AppInitService) {
  return () => appLoadService.initializeApp();
}
import { MidBodyComponent } from './mid-body/mid-body.component';

@NgModule({
  declarations: [
    AppComponent,
    HeaderComponent,
    MidBodyComponent
  ],
  imports: [
    BrowserModule,
    BrowserAnimationsModule,
    FormsModule,
    ReactiveFormsModule,
    HttpModule,
    AppRoutingModule,
    MenubarModule,
    SitesModule,
    HttpClientModule,
    AuthModule,
    ToastModule.forRoot(),
    Ng4LoadingSpinnerModule.forRoot(),
    DialogModule,
    FileUploadModule,
    DropdownModule,
    MenubarModule,
    TableModule,
    ButtonModule,
    AccordionModule,
    InputTextModule,
    PasswordModule,
    TabViewModule,
    DataTableModule
  ],
  providers: [
    AppConstant,
    AppInitService,
    TokenInterceptor,
    TokenService,
    AuthLoginGaurdService,
    AuthGaurdService,
    RedirectionService,
    QuadlogicAuthService,
    UserService,
    PreloaderService,
    {
      provide: HTTP_INTERCEPTORS,
      useClass: TokenInterceptor,
      multi: true
    },

    {
      provide: APP_INITIALIZER,
      useFactory: init_app,
      deps: [AppInitService],
      multi: true
    },
  ],
  bootstrap: [AppComponent]
})
export class AppModule { }



