import { Component, OnInit, Input } from '@angular/core';
import { UserService } from '../services/user.service';
import { ActivatedRoute } from '@angular/router';
import { FormBuilder, FormControl, FormGroup, Validators, AbstractControl, ValidationErrors } from '@angular/forms';
import 'rxjs/Rx';
import { ValidatePassword, matchPassword, emailAvailability, phoneNumberAvailability } from './../../shared/common.validators';
import { log } from 'util';
import { ConfirmationService } from 'primeng/api';

@Component({
  selector: 'quadlogic-overview',
  inputs: ['site'],
  templateUrl: './overview.component.html',
  styleUrls: ['./overview.component.css']
})
export class OverviewComponent implements OnInit {

  userCreateForm: FormGroup;

  constructor(private userService: UserService, private route: ActivatedRoute, private _fb: FormBuilder, private confirmationService: ConfirmationService) { 
    this.userCreateForm = this._fb.group({
      id: new FormControl(),
      firstName: new FormControl(null,Validators.required),
      lastName: new FormControl(null, Validators.required),
      email: new FormControl(null, Validators.required),
      role: new FormControl(null, Validators.required)
    });
  }

  @Input() site: any;

  users: any[];
  display: boolean = false;
  roles: any[];
  site_id: any;
  actions:any[];
  formName:string = "Create";


  ngOnInit() {
    
    this.route.params.subscribe(params => {
      this.site_id = +params['id'];
      this.userService.getUsersAndRoles(this.site_id)
    .subscribe(
      (data)=>{
        this.users = data.users;
        this.roles = data.roles;
      }
    )
    })
  }

  createUser(userCreateForm){
    if(userCreateForm.valid){ 
        this.userService.createUser(Object.assign(userCreateForm.value, {site_id: this.site_id})).subscribe((res:any)=>{
          this.users = res.users;
          this.display = false;
          this.userCreateForm.reset();
        })
    }
  }


showDialog() {
  this.display = true;
}

// sekhar
//  method to trigger a dailog with edit form
edit(user_id: string){
  console.log(user_id);
  this.userService.getUserDetails(user_id).subscribe((res:any)=>{
    this.userCreateForm.setValue({id: res.id, firstName: res.firstName, lastName: res.lastName, 
      email: res.email, role: res.role});
      this.display = true;
      this.formName = "Edit";
  })

}
msgs: any[];
delete(user_id: Number){
  console.log(user_id);
  console.log(this.site_id);
  this.confirmationService.confirm({
    message: 'Are you sure that you want to proceed?',
    header: 'Delete this site',
    icon: 'fa fa-question-circle',
    accept: () => {
      this.userService.deleteUser(user_id, this.site_id).subscribe((res:any)=>{
        this.users = res.users;
      })
    },
    reject: () => {
        this.msgs = [{severity:'info', summary:'Rejected', detail:'You have rejected'}];
    }
  });
}

setActions(rowData: any){
  console.log(rowData);
  this.actions = [
    {label: 'Edit', icon: 'far fa-pencil', command: () => {
        this.edit(rowData.id); 
    }},
    {label: 'Delete', icon: 'far fa-trash', command: () => {
      this.delete(rowData.id);
    }},
  ];
}

}



