import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';
import { AuthGaurdService } from 'app/auth/services/auth-gaurd.service';
import { AuthLoginGaurdService } from 'app/auth/services/auth-login-gaurd.service';
import { AuthModule } from 'app/auth/auth.module';
import { SitesModule } from './sites/sites.module';

// sekhar
// routes for the application
const routes: Routes = [
  {path: '', redirectTo: 'auth', pathMatch: 'full'},
  {
    path: 'sites',
    loadChildren: 'app/sites/sites.module#SitesModule',
    canActivate: [AuthGaurdService],
  },
  {
      path: 'auth',
      canActivate: [ AuthLoginGaurdService ],
      loadChildren: './auth/auth.module#AuthModule'
  },

  { path: '**', redirectTo: 'auth' }
];

@NgModule({
  imports: [
    AuthModule,
    CommonModule,
    SitesModule,
    RouterModule.forRoot(routes)
  ],
  exports: [RouterModule],
  declarations: []
})
export class AppRoutingModule { }



