//Importing Internal modules
  import { Router } from '@angular/router';
  import { Injectable } from '@angular/core';

//Importing Third-party modules

//Imporitng Application Modules

//Importing Application components
  import { ReLoginComponent } from '../../shared/re-login/re-login.component';

//Importing Application Services

//Importing third party services
  import { BsModalService } from 'ngx-bootstrap/modal';
  import { BsModalRef } from 'ngx-bootstrap/modal/bs-modal-ref.service';
  import { DialogService } from "ng2-bootstrap-modal";

@Injectable()
export class RedirectionService {

  user:any;
  modalRef: BsModalRef;

  constructor(private _router:Router,private _dialogService:DialogService) { }

  //Navigating to base route based on role
  navigateToDefaultRoute(role){
     switch (role) {
      case 'Interviewer':
        this._router.navigate(['interviewer']);
        break;
      case 'Recruiter':
        this._router.navigate(['recruiter']);
        break;
      case 'Candidate':
        this._router.navigate(['candidate']);
        break;
      default:
        this._router.navigate(['auth']);
        break;
    }
  }

  //Opening Relogin component dialog
  relogin(title?: string, message?: string): any {
     return this._dialogService.addDialog(ReLoginComponent, { }, { backdropColor: 'rgba(8, 8, 8, 0.61)' });
  }

}



