

// Importing Internal modules
import { FormBuilder, FormControl, FormGroup, Validators, AbstractControl, ReactiveFormsModule } from "@angular/forms";
import { Component, OnInit, Input, TemplateRef, NgZone } from '@angular/core';
import { ActivatedRoute } from "@angular/router";
import { NgForm } from "@angular/forms/src/directives";

// Importing Third-party modules
import { BsModalService } from "ngx-bootstrap/modal";
import { BsModalRef } from "ngx-bootstrap/modal/bs-modal-ref.service";
import { DialogComponent, DialogService } from "ng2-bootstrap-modal";
import { ToastsManager } from "ng2-toastr/ng2-toastr";
import { ToastOptions } from "ng2-toastr/ng2-toastr";

// Imporitng Application Modules

// Importing Application components

// Importing Application Services
import { UserService } from "./../../shared/services/user.service";
import { TokenService } from "../../auth/services/token.service";
import { QuadlogicAuthService } from "../../auth/services/auth.service";

import { PreloaderService } from '../services/preloader.service';



export interface ConfirmModel {}
@Component({
  selector: "eteki-re-login",
  templateUrl: "./re-login.component.html",
  styleUrls: ["./re-login.component.scss"],
  providers:[BsModalRef]
})
export class ReLoginComponent extends DialogComponent<ConfirmModel, boolean> {
  modalRef: BsModalRef;

  signInForm: FormGroup;

  constructor(
    dialogService: DialogService,
    private fb: FormBuilder,
    private tokenService: TokenService,
    private _auth: QuadlogicAuthService,
    private userService: UserService,
    public toastr: ToastsManager,
    private _toastOption: ToastOptions,
    private _preLoader: PreloaderService,
    private zone: NgZone

  ) {
    super(dialogService);
    this.signInForm = this.fb.group({
      signinEmail: new FormControl(null, [Validators.required, Validators.pattern("^[a-z0-9._%+-]+@[a-z0-9.-]+\.[a-z]{2,4}$")]),
      signinPassword: new FormControl(null, Validators.required)
    });

  }

  signIn(signInForm) {
    if (signInForm.valid) {
      let params = {
        email: signInForm.value.signinEmail,
        password: signInForm.value.signinPassword
      };
      this._auth.signIn(params).subscribe((res: Response) => {
        console.log(res);
        if (res["success"]) {
          this.result = true;
          this.close();
          this.tokenService.storeTokens(
            res["authentication_token"],
            res["refresh_token"]
          );
          this.userService.setUser(res["user"]);
          this.toastr.success("Signed in successfully.", "Success");
        } else {
          this.toastr.error(res["message"], "Oops!");
        }
      });
    } else {
      this.setFormTouched(this.signInForm);
    }
  }


  cancel() {
    this.result = false;
    this.close();
  }

  setFormTouched(form_obj) {
    Object.keys(form_obj.controls).forEach(field => {
      const control = form_obj.get(field);
      control.markAsTouched({ onlySelf: true });
    });
  }
}



